  label {
    display: inline-block;
    margin-bottom: 0.5rem;
    font-size: 13px;
    font-weight: 400;
  }
  
  .material-switch {
    display: inline-block;
    position: relative;
    width: 50px;
    height: 24px;
    margin-left: 10px;
    margin-right: 10px;
  }
  
  .material-switch input {
    display: none;
  }
  
  .material-switch label {
    position: absolute;
    top: 0;
    left: 0;
    width: 50px;
    height: 24px;
    background-color: #d9d9d9;
    border-radius: 12px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
  }
  
  .material-switch label::after {
    content: '';
    position: absolute;
    top: 4px;
    left: 4px;
    width: 16px;
    height: 16px;
    background-color: #fff;
    border-radius: 50%;
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
  }
  
  .material-switch input:checked + label {
    background-color: #50c878;
  }
  
  .material-switch input:checked + label::after {
    -webkit-transform: translateX(26px);
            transform: translateX(26px);
  }
  
  .material-switch label.bg-primary {
    background-color: #32cd32;
  }
  
  .material-switch input:checked + label.bg-primary {
    background-color: #00ff7f;
  }
  
  .material-switch label.bg-secondary {
    background-color: #5d5959;
  }
  
  .material-switch input:checked + label.bg-secondary {
    background-color: #40e0d0;
  }
